.crop-container {
	position: relative;
	width: 100%;
	height: 100%;
	user-select: none;
	touch-action: none;
	cursor: move;
	display: flex;
	justify-content: center;
	align-items: center;
	box-sizing: border-box;
}

.crop-media {
	width: 100%;
	will-change: transform;
	box-sizing: border-box;
}

.grid-thirds {
	position: absolute;
	top: 0;
	bottom: 0;
	left: 0;
	right: 0;
	border: 1px solid rgba(255, 255, 255, 0.5);
	box-sizing: border-box;
	overflow: hidden;
}

.grid-thirds::before {
	content: "";
	box-sizing: border-box;
	position: absolute;
	border: 1px solid rgba(255, 255, 255, 0.5);
	top: 0;
	bottom: 0;
	left: 33.33%;
	right: 33.33%;
	border-top: 0;
	border-bottom: 0;
}

.grid-thirds::after {
	content: "";
	box-sizing: border-box;
	position: absolute;
	border: 1px solid rgba(255, 255, 255, 0.5);
	top: 33.33%;
	bottom: 33.33%;
	left: 0;
	right: 0;
	border-left: 0;
	border-right: 0;
}

html,
body,
#root {
	box-sizing: border-box;
	height: 100%;
	width: 100%;
}

#root {
	overflow: hidden;
	display: flex;
	justify-content: center;
	align-items: center;
	background: #777;
}
